.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.main-div {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  text-decoration: none;
  color: #75a0f7;
}
#footer {
  font-size: calc(2.5px + 2vmin);
}
.quote-frame {
  background-color: #0c5ab4;
  min-height: 35vh;
  width: 35vw;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  text-align: center;
  font-size: calc(15px + 2vmin);
  color: white;
  border-radius: 2.5%;
}

.Quote {
  padding: 7.5%;
  font-size: inherit;
}

#author {
  text-align:right;
  font-size: calc(5px + 2vmin);
  line-height:12px;
}

.links {
  padding-bottom: 3%;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  font-size: calc(7.5px + 2vmin);
}

.link-button {
  margin-left: 5%;
}

#tweet-quote {
  color: white;
}

#tumblr {
  color: white;
}
.new-button {
  color: brown;
  position: absolute;
  margin-left: 25%;

}